@font-face{font-family:"Ferry";src:url("../public/fonts/ferry_black.otf")}*{box-sizing:border-box;margin:0;font-family:"Inter",Arial,Helvetica,sans-serif}body{width:100%;overflow-x:hidden}.root{display:flex;flex-direction:column;min-height:100vh}main{flex-grow:1}.container{width:min(1360px,100% - 40px);margin-inline:auto}.h{font-family:"Ferry",Tahoma,Verdana,sans-serif}.h1{font-size:60px}.h2{font-size:45px}.h3{font-size:40px}.h4{font-size:30px}.h5{font-family:"Inter",Arial,Helvetica,sans-serif;font-size:30px}.h6{font-family:"Inter",Arial,Helvetica,sans-serif;font-size:26px;font-weight:600}@keyframes rotate{0%{transform:rotate(0deg)}40%{transform:rotate(180deg)}60%{transform:rotate(220deg)}100%{transform:rotate(360deg)}}.spinner img{width:40px;height:40px;animation:rotate 2s infinite linear}@media screen and (max-width: 1020px){.h1{font-size:40px}.h2{font-size:35px}.h3{font-size:28px}.h4{font-size:25px}}@media screen and (max-width: 767px){.h1,.h2{font-size:28px}.h3{font-size:22px}.h4{font-size:20px}}.hom_sec3_tariffs{display:flex;justify-content:space-between;gap:20px;margin:0 0 120px;font-size:18px;align-items:stretch}.hom_sec3_tariffs .current{border:2px solid var(--background)}.hom_sec3_tariffs .tarif{flex:1;min-width:280px;box-shadow:0 0 20px rgba(0,0,0,.2);border-radius:10px;overflow:hidden;margin-top:70px;transition:all .3s ease;position:relative;background:#fff;display:flex;flex-direction:column}.hom_sec3_tariffs .tarif:hover{transform:translateY(-10px);box-shadow:0 10px 30px rgba(0,0,0,.3)}.hom_sec3_tariffs .tarif:hover .buttonTariff{background-color:#2644ff;transform:scale(1.02)}.hom_sec3_tariffs .tarif .sec3_h5{margin-bottom:10px}.hom_sec3_tariffs .tarif .title{display:flex;justify-content:space-between;color:var(--color);background-color:var(--background);transition:background-color .3s ease;min-height:150px;flex-shrink:0}.hom_sec3_tariffs .tarif .title .title_text{margin:30px 0 34px 30px;flex:1}.hom_sec3_tariffs .tarif .title .img{margin:11px 15px 0 0;transition:transform .3s ease;flex-shrink:0}.hom_sec3_tariffs .tarif:hover .title .img{transform:scale(1.1) rotate(5deg)}.hom_sec3_tariffs .tarif .currentTariff{height:24px;margin-top:12px;display:flex;justify-content:right;flex-shrink:0}.hom_sec3_tariffs .tarif .currentTariff .badge{color:#fff;background-color:#3ba5e0;border-radius:10px;height:inherit;padding:0 13px;margin-right:10px;font-size:14px;vertical-align:middle;display:flex;align-items:center}.hom_sec3_tariffs .tarif .text{margin:0 30px;flex-shrink:0}.hom_sec3_tariffs .tarif .text .oldPrice{opacity:50%;text-decoration:line-through;font-size:25px}.hom_sec3_tariffs .tarif .ul{padding:0;margin-top:59px;flex-grow:1}.hom_sec3_tariffs .tarif .ul .ul_title{font-size:20px;font-weight:bold;margin-bottom:10px}.hom_sec3_tariffs .tarif .ul .li{list-style-type:none;transition:transform .2s ease;margin-bottom:8px}.hom_sec3_tariffs .tarif .ul .li:hover{transform:translateX(5px)}.hom_sec3_tariffs .tarif .ul .li:before{content:url(images/greenСheckmark.png);width:20px;height:20px;margin-right:8px;vertical-align:middle;transition:transform .3s ease}.hom_sec3_tariffs .tarif .ul .li:hover:before{transform:scale(1.2)}.hom_sec3_tariffs .tarif .buttonTariff{display:flex;justify-content:center;align-items:center;text-decoration:none;background-color:#5970ff;width:calc(100% - 60px);height:59px;margin:25px 30px 25px;border:none;border-radius:5px;color:#fff;font-size:20px;cursor:pointer;transition:all .3s ease;flex-shrink:0}.hom_sec3_tariffs .tarif .buttonTariff:hover{box-shadow:0 5px 15px rgba(89,112,255,.4)}.hom_sec3_tariffs .tarif .buttonCurrent{background-color:#d2d2d2;color:#000}@media screen and (max-width: 1234px){.hom_sec3_tariffs{flex-wrap:wrap;justify-content:center;margin-top:70px;margin-bottom:70px;gap:30px}.hom_sec3_tariffs .tarif{flex:0 0 calc(50% - 20px);margin:0}}@media screen and (max-width: 1020px){.hom_sec3_tariffs{flex-direction:row;flex-wrap:wrap;margin:70px 0 70px;gap:30px}.hom_sec3_tariffs .tarif{flex:0 0 calc(50% - 15px);margin:0;max-width:100%}.hom_sec3_tariffs .tarif .title{min-height:150px}.hom_sec3_tariffs .tarif .title .title_text{max-width:none}.hom_sec3_tariffs .tarif .ul{min-height:auto}.hom_sec3_tariffs .tarif .installment{max-width:400px}}@media screen and (max-width: 767px){.hom_sec3_tariffs{margin:40px 0 40px;flex-direction:column;align-items:center}.hom_sec3_tariffs .tarif{width:100%;margin:0;flex:0 0 auto}.hom_sec3_tariffs .tarif:hover{transform:translateY(-5px)}}@media screen and (max-width: 480px){.hom_sec3_tariffs .tarif{margin:0}.hom_sec3_tariffs .tarif .title{min-height:120px;flex-direction:column}.hom_sec3_tariffs .tarif .title .title_text{margin:20px}.hom_sec3_tariffs .tarif .title .img{margin:0 20px 20px auto}.hom_sec3_tariffs .tarif .text{margin:0 20px}.hom_sec3_tariffs .tarif .buttonTariff{margin:25px 20px;width:calc(100% - 40px)}}