@import '../../../../../App.scss';

.button {
    width: 100%;
    height: 59px;
    border-radius: 5px;
    border: none;
    font-size: 22px;
    color: $prime_3;
    margin: 11px 0 15px;
    background-color: $supplemental_3;
    position: relative;
    cursor: pointer;
}

.button:disabled {
    background-color: rgba($supplemental_3, 0.5);
}

.buttonSearch {
    margin: 11px 0 10px;
}

.btnObjectSearch {
    width: 305px;
    display: block;
    margin: 0 auto 60px;
}

@media screen and (max-width: 767px) {
    .btnObjectSearch {
        margin: 0 auto 20px;
    }
}

@media screen and (max-width: 400px) {
    .btnObjectSearch {
        width: 100%;
    }
}